const image = () => {
  return (
    <div style={{border: '1px solid gray', width: '250px'}}>
      <img style={{ width: '250px', height: 'auto' }} src='https://images.pexels.com/photos/406014/pexels-photo-406014.jpeg' />
    </div>
  );
};

export default image;



